.promises {
	background-image: url('../img/bg-promises.png');
	@include cover;
	padding: 70px 0 75px 0;
	text-align: center;
	@include r(768) {
		background-image: none;
		background-color: #1e1e1e;
		padding: 30px 0; }
	h2 {
		font-size: 40px;
		color: $white;
		display: inline-block;
		@include font-family($circle-reg);
		@include r(992) {
			font-size: 32px; }
		@include r(768) {
			font-size: 26px; }
		span {
			@include font-family($circle-bld);
			font-weight: bold;
			color: $orange; } }
	ul {
		font-size: 0;
		display: flex;
		justify-content: space-between;
		flex-direction: row;
		margin-top: 60px;
		margin-bottom: 100px;
		@include r(768) {
			flex-direction: column;
			margin: 50px auto;
			align-items: center; } }
	ul li {
		max-width: 300px;
		text-align: left;
		@include r(768) {
			text-align: center;
			max-width: 100%;
			margin-bottom: 30px; }
		i {
			display: block;
			margin-left: 0;
			margin-bottom: 40px;
			@include r(768) {
				margin: 0 auto 20px; } }
		p {
			font-size: $text-md; //18px
			line-height: 1.625;
			color: $white;
			span {
				color: $orange;
				display: block; } } }
	&__author {
		display: inline-block;
		vertical-align: top;
		font-size: 0;
		@include clr;
		&-photo {
			float: left;
			width: 265px;
			height: 265px;
			@include cover;
			margin-right: 70px;
			@include r(768) {
				margin: 0 auto 30px;
				float: none; } }
		&-text {
			text-align: left;
			margin-left: 335px;
			padding-top: 30px;
			@include r(768) {
				float: none;
				margin: 0 auto;
				max-width: 90%; }
			p {
				color: $white;
				font-size: $text-md; //18px
				line-height: 1.625;
				@include r(768) {
					font-size: 16px; }
				&:not(:last-child) {
					margin-bottom: 30px; } } } } }


//special
.special {
	padding-top: 65px;
	padding-bottom: 105px;
	background-color: $white-f3;
	@include r(992) {
		padding: 30px 0; }
	&__title {
		font-size: $text-lg; //18px
		color: $black-c0;
		line-height: 1;
		margin-bottom: 52px;
		@include font-family($circle-bld);
		font-weight: bold;
		@include r(768) {
			margin-bottom: 32px; }
		&.title-lg {
			font-size: 34px;
			margin-bottom: 32px;
			@include r(768) {
				font-size: 28px; }
			@include r(568) {
				font-size: 24px; }
			@include r(480) {
				font-size: 22px; }
			span {
				display: inline-block; } } }
	&__items {
		display: flex;
		justify-content: space-between;
		flex-direction: row;
		max-width: 830px;
		margin: 0 auto;
		@include r(992) {
			flex-direction: column;
			align-items: center; }
		&.justify-center {
			justify-content: center;
			.special__item {
				max-width: 100%; }
			.special__box {
				max-width: 370px;
				margin: 0 auto; } } }
	&__item {
		max-width: 370px;
		width: 100%;
		text-align: center;
		@include r(992) {
			margin-bottom: 60px; }

		.title-theme {
			font-size: 24px;
			color: $black-c0;
			margin-bottom: 52px; } }
	&__box {
		background-color: $white;
		padding: 30px 0 60px 0;
		position: relative;
		.btn-gradient {
			position: absolute;
			bottom: -25px;
			transform: translateX(-50%);
			left: 50%; }
		p {
			color: $black-c0;
			font-size: $text-md; //18px
			line-height: 1;
			&.text-md {
				font-size: 20px;
				color: $gray-89;
				margin: 30px auto 20px; }
			&.text-lg {
				color: $black-c0;
				font-size: 60px;
				position: relative;
				display: block;
				&:after {
					content: '';
					display: block;
					position: absolute;
					top: 50%;
					left: 50%;
					width: 80%;
					height: 11px;
					transform: translate(-50%, -50%);
					box-shadow: 0 0 30px $white; } } } } }

// sponsion
.sponsion {
	padding: 100px 0;
	background-image: url('../img/bg-sponsion.jpg');
	@include cover;
	position: relative;
	overflow: hidden;
	@include r(768) {
		padding: 40px 0; }
	&__text {
		padding-left: 30px;
		margin-left: 30px;
		max-width: 800px;
		position: relative;
		@include r(768) {
			margin-left: 0; }
		&:before {
			content: "";
			position: absolute;
			left: 0;
			top: 0;
			height: 100%;
			width: 8px;
			background-image: -moz-linear-gradient( 90deg, rgb(225,0,25) 0%, rgb(252,96,0) 100%);
			background-image: -webkit-linear-gradient( 90deg, rgb(225,0,25) 0%, rgb(252,96,0) 100%);
			background-image: linear-gradient( 90deg, rgb(225,0,25) 0%, rgb(252,96,0) 100%); }
		h3 {
			color: $white;
			@include font-family($circle-bld);
			font-weight: bold;
			font-size: 24px;
			margin-bottom: 40px; }
		p {
			font-size: $text-md;
			color: $white; } }
	.pic-sponsion {
		position: absolute;
		right: 15%;
		opacity: .27;
		top: -25px;
		pointer-events: none;
		@include r(1200) {
			display: none; } } }


// authores
.authores {
	background-color: $white;
	padding: 100px 0;
	text-align: center;
	@include r(992) {
		padding: 40px 0; }
	.read {
		margin-bottom: 80px;
		@include r(992) {
			flex-direction: column;
			justify-content: center;
			align-items: center; }
		&__box {
			p {
				@include r(768) {
					font-size: 16px; } }
			.btn.btn-gradient {
				@include r(992) {
					padding: 13px 20px; } }
			@include r(992) {
				margin: 0 auto;
				padding: 30px;
				height: auto; } }
		&__col h3 {
			@include r(992) {
				margin-top: 20px;
				font-size: 18px; }
			@include r(768) {
				font-size: 16px; } } }
	.list-info {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		margin-top: 65px;
		@include r(992) {
			flex-direction: column;
			margin-top: 30px; }
		li {
			text-align: left;
			width: 30%;
			@include r(992) {
				width: 100%; }
			> ul li {
				display: block;
				width: 100%;
				position: relative;
				padding-left: 20px;
				&:before {
					content: '';
					display: block;
					position: absolute;
					left: 0;
					top: 5px;
					@include arr(8, 10, $orange, r); } }

			> ul li p {
				font-size: $text-md; //18px
				@include font-family($circle-reg);
				color: $black-c0;
				margin-bottom: 10px;
				&.mb-2x {
					margin-bottom: 32px; } } }
		li .title-text {
			@include font-family($circle-bld);
			font-weight: bold;
			font-size: 20px;
			margin-bottom: 40px; } } }

// read
.read {
	font-size: 0;
	display: flex;
	justify-content: space-between;
	flex-direction: row;
	&-overlay {
		display: block;
		position: absolute;
		bottom: 0;
		left: 0;
		height: 50%;
		width: 100%;
		background-color: rgba($white, .81); }
	&__col {
		text-align: center;
		&.read-lg {
			width: 100%;
			text-align: left;
			h3 {
				margin-left: 90px; }
			.read__box {
				max-width: 100%;
				height: auto;
				min-height: 315px; } }
		h3 {
			margin-bottom: 52px;
			color: $black-c0;
			font-size: 34px;
			@include font-family($circle-bld);
			font-weight: bold; } }
	&__box {
		text-align: left;
		padding: 45px 60px 20px;
		box-shadow: 0px 0px 35px 0px rgba(0, 0, 0, 0.031);
		max-width: 500px;
		box-sizing: border-box;
		height: 470px;
		position: relative;
		.btn {
			position: absolute;
			bottom: 70px;
			z-index: 9999;
			left: 50%;
			transform: translateX(-50%); } }
	&__box {
		p {
			font-size: $text-md; //18px
			@include font-family($circle-reg);
			color: $black-c0;
			margin-bottom: 36px;
			&:last-child {
				margin-bottom: 0; } } } }

.slider {
	&-author {
		position: relative;
		margin-top: 100px;
		.aside {
			position: absolute;
			top: -35px;
			right: 0;
			width: 100%;
			max-width: 270px;
			box-shadow: 0 0 35px 0 rgba(0,0,0,.031);
			background-color: $white;
			@include r(992) {
				max-width: 60%;
				margin: 30px auto;
				position: static; }
			@include r(568) {
				max-width: 100%; }
			&__avatar {
				@include cover;
				width: 103px;
				display: block;
				position: absolute;
				top: 30px;
				left: 50%;
				transform: translateX(-50%);
				height: 103px; }
			&__heading {
				background-color: $black-c0;
				height: 80px;
				display: block;
				position: relative;
				&:after {
					content: '';
					display: block;
					position: absolute;
					top: -4px;
					left: 50%;
					width: 170px;
					height: 8px;
					transform: translateX(-50%);
					background-image: linear-gradient( 4deg, rgb(255,25,25) 0%, rgb(255,134,15) 100%);
					background-image: -mos-linear-gradient( 4deg, rgb(255,25,25) 0%, rgb(255,134,15) 100%);
					background-image: -webkit-linear-gradient( 4deg, rgb(255,25,25) 0%, rgb(255,134,15) 100%);
					background-image: -ms-linear-gradient( 4deg, rgb(255,25,25) 0%, rgb(255,134,15) 100%); } }
			&__body {
				padding: 75px 50px 50px;
				@include r(768) {
					padding: 25px;
					margin-top: 50px; }
				@include r(568) {
					padding: 15px; }
				p {
					color: $black-c0;
					font-size: $text-reg;
					&.position {
						margin: 30px auto;
						display: block; }
					&.name {
						font-weight: bold; } } } } }
	&__quote {
		font-size: 0;
		text-align: left;
		max-width: 550px;
		margin: auto 0;
		padding-top: 40px;
		position: relative;
		&:before {
			content: '“';
			font-size: 183px;
			color: $gray;
			display: block;
			position: absolute;
			top: -15px;
			left: -45px;
			opacity: .10; }
		p {
			font-size: $text-md;
			color: $black-c0; } }
	&__heading {
		height: 78px;
		line-height: 78px;
		justify-content: space-between;
		display: flex;
		background-image: url('../img/shape-heading.png');
		@include cover;
		flex-direction: row;
		padding: 0 110px;
		@include r(992) {
			padding: 0 50px; }
		@include r(768) {
			padding: 0 25px; }
		@include r(480) {
			justify-content: center; } }
	&__name {
		font-size: 20px; }
	&__counter {
		margin-right: 120px;
		font-size: 0;
		z-index: 9999;
		color: $black;
		position: absolute;
		top: 20px;
		right: 200px;
		@include r(992) {
			margin-right: 0;
			right: 30px; }
		@include r(480) {
			display: none; }
		i {
			display: inline-block;
			vertical-align: middle; }
		p {
			font-size: 26px;
			color: $white;
			display: inline-block;
			vertical-align: middle; } }
	&__name,
	&__counter {
		display: inline-block;
		vertical-align: top;
		color: $white; }
	&__author {
		font-size: 0; }
	&__item {
		display: inline-block;
		width: 100%;
		position: relative; }
	&__photo {
		@include cover;
		width: 210px;
		height: 210px;
		display: inline-block;
		vertical-align: top;
		position: absolute;
		top: 0;
		left: 0;
		@include r(992) {
			display: none; } }
	&__field {
		display: inline-block;
		vertical-align: top;
		width: 100%;
		max-width: 800px; }
	&__field table {
		width: 80%;
		margin: 30px auto;
		position: relative;
		@include r(992) {
			width: 100%; }
		&:after {
			content: '';
			display: block;
			position: absolute;
			bottom: -30px;
			width: calc(100% - 60px);
			left: 0;
			height: 1px;
			background-color: $gray; }
		tr td {
			font-size: 18px;
			color: $black-c0;
			padding: 0 25px;
			text-align: left;
			@include r(480) {
				padding: 0 5px; } }
		tbody tr td {
			@include font-family($circle-bld);
			font-weight: bold; }
		thead tr td {
			@include font-family($circle-reg); } } }


// reviews
.reviews {
	text-align: center;
	h3 {
		font-size: 28px;
		margin-bottom: 58px;
		margin-top: 78px; }
	.read {
		&__box {
			@include r(992) {
				padding: 30px; } } } }
.slider {
	&-reviews {
		.slider__item ul {
			@include justify-between; }
		.slider__item ul li {
			font-size: 0;
			@include r(1200) {
				padding: 0 20px; }
			img {
				max-width: 100%;
				height: auto; } }
		.slick-dots {
			display: flex;
			justify-content: space-between;
			width: 100%;
			max-width: 160px;
			margin: 32px auto 72px;
			li {
				display: inline-block;
				width: 20px;
				height: 20px;
				border-radius: 50px;
				background-color: $white;
				border: 2px solid $black-c0;
				&.slick-active {
					background-color: $orange; } }
			li button {
				font-size: 0;
				border-color: transparent; } } } }

.btn {
	&-prev,
	&-next {
		background-color: transparent;
		cursor: pointer;
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		z-index: 9999; }
	&-prev {
		left: -98px;
		@include r(1200) {
			left: -30px; } }
	&-next {
		right: -98px;
		@include r(1200) {
			right: -30px; } } }


.pack {
	.inner {
		@include r(768) {
			padding: 0 10px; } }
	&__content {
		margin: 270px 0 220px;
		position: relative;
		@include r(992) {
			overflow-x: scroll;
			margin: 100px 0; } }
	&__body {
		min-width: 1030px;
		@include r(992) {
			padding-top: 200px; }
		ul {
			position: relative; }
		ul li {
			font-size: 16px;
			color: $gray-2e;
			text-align: left;
			padding: 26px 0 16px 0;
			@include clr;
			display: flex;
			flex-direction: row;
			justify-content: space-between;
			> ul {
				display: flex;
				justify-content: space-between;
				flex-direction: row;
				li {
					padding: 0;
					height: 100%;
					width: 200px;
					display: flex;
					justify-content: center;
					align-items: center;
					flex-direction: column;
					text-align: center;
					&:nth-child(even) {
						background-color: transparent; }
					&:nth-child(odd) {
						background-color: transparent; } }
				li strong {
					font-size: 16px;
					color: $black-c0;
					display: block;
					em {
						display: block;
						color: $gray-a1;
						font-weight: normal;
						font-style: normal; } }
				li p {
					font-weight: bold;
					span {
						font-size: 18px;
						text-decoration: line-through;
						color: $gray-ac; }
					em {
							font-size: 34px;
							color: $red;
							font-style: normal; } } }
			p {
				max-width: 430px;
				width: 100%;
				display: inline-block;
				vertical-align: middle;
				padding: 0 20px; }
			&:nth-child(even) {
				background-color: $white; }
			&:nth-child(odd) {
				background-color: #fcfcfc; } } }
	&__borderShadow {
		position: absolute;
		top: 0;
		height: 100%;
		width: 200px;
		right: 200px;
		pointer-events: none;
		box-shadow: 0px 0px 35px 0px rgba(0, 0, 0, 0.031); }
	&__status {
		position: absolute;
		top: -200px;
		right: 0;
		background-color: transparent !important;
		padding: 0 !important;
		.status {
			width: 200px;
			height: 200px;
			@include cover;
			justify-content: center;
			align-items: center;
			display: flex;
			flex-direction: column;
			padding: 0 30px;
			small {
				font-size: 14px; }
			&__position {
				font-size: 40px;
				font-weight: bold;
				@include font-family($circle-bld); }
			&__text {
				font-size: 14px; }
			&_silver {
				background-image: url('../img/status-silver.png');
				color: $white; }
			&_gold {
				color: $white;
				background-image: url('../img/status-gold.png'); }
			&_plat {
				color: $black-c0;
				background-image: url('../img/status-plat.png'); } } }
	&__link {
		top: calc(100% + 75px);
		right: 0;
		position: absolute;
		background-color:  transparent !important;
		ul li a {
			color: #888888;
			font-size: 16px;
			font-weight: bold;
			@include font-family($circle-bld); } }
	&__box {
		position: absolute;
		top: 100%;
		right: 0;
		padding: 0 !important;
		background-color: transparent;
		ul li {
			.box {
				width: 100%;
				@include cover;
				transition: background-color .24s ease-in-out;
				a {
					text-decoration: none;
					font-size: 24px;
					display: block;
					font-weight: bold;
					padding: 26px 0 16px;
					transition: color .24s ease-in-out;
					@include font-family($circle-bld); }
				&_silver {
					background-color: #252525;
					background-image: url('../img/shape-silver.png');
					&:hover {
						background-image: none;
						background-color: lighten(#252525, 20); }
					a {
						color: $white; } }
				&_gold {
					background-color: #f53300;
					background-image: url('../img/shape-gold.png');
					&:hover {
						background-image: none;
						background-color: lighten(#f53300, 20); }
					a {
						color: $white; } }
				&_platinum {
					background-color: #eeeeee;
					background-image: url('../img/shape-platinum.png');
					&:hover {
						background-image: none;
						background-color: darken(#eeeeee, 20);
						a {
							color: $white; } }
					a {
						color: $black; } } } } }
	&__item {
		@include cover;
		display: block;
		width: 200px;
		height: 200px; }
	&__content table {
		box-shadow: 0px 0px 35px 0px rgba(0, 0, 0, 0.031);
		tr td {
			width: 200px;
			vertical-align: middle;
			text-align: center;
			span {
				display: block;
				color: $gray-a1;
				font-size: 16px;
				@include font-family($circle-bld);
				font-weight: bold; }
			strong {
				font-weight: bold;
				@include font-family($circle-bld);
				font-size: $text-md; } //18px
			&:first-child {
				font-size: 16px;
				color: $gray-2e;
				width: 460px;
				text-align: left;
				padding: 26px 45px 16px 60px; } }
		tr {
			&:nth-child(even) {
				background-color: $white; }
			&:nth-child(odd) {
				background-color: #fcfcfc; } } } }

.timer {
	display: inline-block; }
div[class*="t-"] {
	display: inline-block;
	color: $white;
	font-size: 39px; }
.t {
	&-days,
	&-hours,
	&-mins {
		position: relative;
		&:after {
			content: ':';
			display: block;
			position: absolute;
			bottom: 0;
			left: 100%;
			font-size: 100%;
			color: $white; } } }
